<template>
  <div id="background" class="main-content">
    <div class="app flex-row align-items-center">
      <div class="container">
        <b-row class="justify-content-center mb-5">
          <b-col md="6" lg="4" xl="4">
            <div class="text-center mb-4">
              <img
                class="navbar-brand-full p-3"
                src="@/assets/logo-w.svg"
                width="60%"
                alt="Expense Reduction Management Logo"
              />
            </div>

            <b-card-group>
              <b-card no-body class="p-1 pc-view">
                <b-card-body class="card-padding-top-left pl-0 pr-0">
                  <b-row v-show="showMsg">
                    <b-col cols="12">
                      <div class="w-full d-flex justify-content-center">
                        <i
                          class="fa fa-check-circle-o fa-4x"
                          aria-hidden="true"
                          style="color: green; font-size: 80px"
                        ></i>
                      </div>
                      <div class>
                        {{ $t("PASSWORDRESETLINKSENT1") }} {{ email }}
                        {{ $t("PASSWORDRESETLINKSENT2") }}
                      </div>
                      <div class>{{ $t("PASSWORDRESETLINKSENT3") }}</div>
                    </b-col>
                  </b-row>
                  <b-form @submit.prevent="submit" v-show="!showMsg">
                    <div class="text-center title-fw title-fs txtColor">
                      {{ $t("RESETPASSWORDHEADER") }}
                    </div>
                    <p
                      v-if="validationMessage !== ''"
                      class="text-center text-danger"
                    >
                      {{ validationMessage }}
                    </p>
                    <b-row class="mb-3 mt-1">
                      <b-col>
                        <div v-if="!isAdmin" class="mb-2 text-center linkTxt">
                          {{ $t("RESETPASSWORDLEBEL") }}
                        </div>
                        <div v-else class="mb-2 text-center linkTxt">
                          {{ $t("ADMINRESETPASSWORDLEBEL") }}
                        </div>
                      </b-col>
                    </b-row>
                    <b-form-group>
                      <b-row class="mr-2 ml-4">
                        <b-col cols="12">
                          <b-button
                            variant="primary"
                            class="px-4 required-btn"
                            >{{ $t("REQUIREDBUTTON") }}</b-button
                          >
                          <label class="line-3 ml-1 txtColor">
                            {{ $t("REGISTEREDMAILADDRESS") }}
                          </label>
                        </b-col>
                        <b-col cols="12">
                          <b-input-group class="mb-1 mt-1">
                            <b-form-input
                              v-model.trim="$v.email.$model"
                              type="text"
                              class="form-control sp-view"
                            />
                          </b-input-group>
                          <show-errors
                            :control="$v.email"
                            :field="$t('REGISTEREDMAILADDRESS')"
                          />
                        </b-col>
                      </b-row>
                    </b-form-group>

                    <b-row>
                      <b-col cols="12" class="text-center mb-1">
                        <b-button
                          :disabled="disabled"
                          @click="submit"
                          variant="primary"
                          type="button"
                          class="px-4 reset-btn"
                        >
                          {{ $t("SEND") }}
                          <i v-if="loading" class="fa fa-spinner fa-spin" />
                        </b-button>
                      </b-col>
                    </b-row>
                  </b-form>
                  <b-modal
                    v-model="show"
                    centered
                    variant="secondary"
                    modal-class="py-0"
                    body-class="py-0"
                    size="md"
                    footer-class="border-0"
                    header-class="border-0 bg-white"
                  >
                    <template v-slot:modal-header="{ cancel }">
                      <div class="w-100 d-flex justify-content-start">
                        <button
                          @click="cancel()"
                          class="btn-sm float-left btn-custom-color px-1"
                        >
                          {{ $t("CLOSE") }}
                        </button>
                      </div>
                    </template>
                    <div class="my-0 py-0 px-2">
                      {{ successMsgC }}
                      <p>{{ successMsgD }}</p>
                    </div>
                    <template v-slot:modal-footer="{ cancel }">
                      <div class="w-100">
                        <button
                          @click="cancel()"
                          class="btn-sm float-left btn-custom-color px-1 d-none"
                        >
                          {{ $t("CLOSE") }}
                        </button>
                      </div>
                    </template>
                  </b-modal>
                </b-card-body>
              </b-card>
            </b-card-group>
          </b-col>
        </b-row>
        <div class="justify-content-center text-center footerDispaly">
          <span class="text-center textColor">{{ $t("COPYRIGHT") }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import i18n from "@/plugins/i18n";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "UserResetPasswordRequest",
  components: { ShowErrors },
  data() {
    return {
      email: "",
      loading: false,
      disabled: false,
      successMsg: "",
      showMsg: false,
      show: false,
      validationMessage: "",
    };
  },
  validations: {
    email: {
      required,
      email,
    },
  },
  computed: {
    successMsgC: () => {
      return i18n.t("PASSWORDRESETLINKSENT1");
    },
    successMsgD: () => {
      return i18n.t("PASSWORDRESETLINKSENT2");
    },
    isAdmin() {
      return localStorage.getItem("role") === "ADMIN";
    },
  },
  watch: {
    email: {
      handler() {
        this.clearApiValidation();
      },
      deep: true,
    },
  },
  beforeCreate() {
    if (window.location.pathname.includes("mng")) {
      localStorage.setItem("role", "ADMIN");
      this.access_role = "ADMIN";
    } else {
      localStorage.setItem("role", "USER");
      this.access_role = "USER";
    }
  },
  methods: {
    async submit() {
      this.$v.$touch();
      /* If valid it will navigate to success screen */
      if (!this.$v.$invalid) {
        this.loading = true;
        this.disabled = true;
        if (localStorage.getItem("role") == "USER") {
          const { data } = await BasicInfoApis.postAPI(
            endPoint.FORGOTPASSWORD,
            {
              remind_mail: this.email,
            }
          );
          this.loading = false;
          this.disabled = false;

          const successMessage =
            i18n.t("PASSWORDRESETLINKSENT1") +
            this.email +
            i18n.t("PASSWORDRESETLINKSENT2");

          if (data.success) {
            this.$swal({
              width: 700,
              type: "success",
              html:
                "<p class='text-left'>" +
                successMessage +
                "</p>" +
                "<p class='text-left'>" +
                i18n.t("PASSWORDRESETLINKSENT3") +
                "<p>",
              customClass: {
                popup: "format-pre",
              },
            });
          } else {
            if (data.message.code == 2002) {
              return (this.validationMessage = this.$t("backend.1008"));
            }
            this.validationMessage = this.$t("backend." + data.message.code);
          }
        } else {
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MFORGOTPASSWORD,
            {
              remind_mail: this.email,
            }
          );
          this.loading = false;
          this.disabled = false;

          const successMessage =
            i18n.t("PASSWORDRESETLINKSENT1") +
            this.email +
            i18n.t("PASSWORDRESETLINKSENT2");

          if (data.success) {
            this.$swal({
              width: 700,
              type: "success",
              html:
                "<p class='text-left'>" +
                successMessage +
                "</p>" +
                "<p class='text-left'>" +
                i18n.t("PASSWORDRESETLINKSENT3") +
                "<p>",
              customClass: {
                popup: "format-pre",
              },
            });
          } else {
            if (data.message.code == 2002) {
              return (this.validationMessage = this.$t("backend.1008"));
            }
            this.validationMessage = this.$t("backend." + data.message.code);
          }
        }
      }
    },

    clearApiValidation() {
      this.validationMessage = "";
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
.reset-btn {
  padding: 10px 50px 10px 50px !important;
  min-width: 200px !important;
  border-radius: 4px;
}

.line-3 {
  line-height: 2.4;
}
.p-top-5 {
  padding: 0.55rem 0rem;
}
.card-padding-top {
  padding-top: 0rem;
}
.format-pre p {
  font-size: 14px;
}

.btn-custom-color {
  background-color: #062854;
  border-color: #062854;
  color: white;
}

.btn-custom-color:hover {
  color: white;
}
.linkTxt {
  color: var(--unnamed-color-003170);
  font-size: 10px;
  letter-spacing: 0px !important;
  color: #003170 !important;
}
.form-control {
  border-radius: inherit;
  height: 30px;
  background-color: rgb(248, 248, 248);
}
.txtColor {
  color: #003170 !important;
  font-weight: 600 !important;
}
.footerDispaly {
  font: normal normal bold 12px/23px YuGothic;
  margin-top: 2rem;
  color: #fff;
}
.pc-view {
  background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #0000000f;
  border-radius: 10px;
  opacity: 1;
}
.sp-view {
  background: #f7f7f7 0% 0% no-repeat padding-box;
  border-radius: 4px;
  opacity: 1;
  height: 40px !important;
  border: none !important;
}
@media (max-width: 991px) {
  .sp-view {
    font-size: 16px;
  }
}
</style>
